import React from 'react';
import './center-content.css'
import axiosConfig from "../../axiosConfig";
function Login() {
    function saveConstantValueToLocalStorage() {
        const nazwaUzytkownika = 'Jan Kowalski';
        localStorage.setItem('userName', nazwaUzytkownika);
        localStorage.setItem('pass', 'aktywny');
        localStorage.setItem('peoplesInGym', '21');
        localStorage.setItem('ROLE', 'client');
    }
    const handleSaveClick = () => {
        saveConstantValueToLocalStorage();

        window.location.reload();
    };

    return (
        <div>
            <button onClick={handleSaveClick}>Zapisz stałą wartość do localStorage</button>
        </div>
    );
}

export default Login;
