import React from 'react';
import Card from 'react-bootstrap/Card';
import Button from "react-bootstrap/Button";

function CourseCards() {
    const dataFromDatabase = [
        {
            id: 1,
            title: "Joga",
            subtitle: "Anna Kowalska",
            text: "Zapraszamy na wyjątkową podróż do harmonii ciała i umysłu z Anną Kowalską, naszym doświadczonym instruktorem jogi. Na naszych zajęciach jogi Anna skupia się na stworzeniu przestrzeni dla spokoju i równowagi w Twoim życiu. ",
        },
        {
            id: 2,
            title: "Aerobik",
            subtitle: "Marek Nowak",
            text: " Popraw swoją wytrzymałość, kondycję i samopoczucie, bawiąc się na pełnych energii treningach aerobikowych. Marek jest źródłem inspiracji i motywacji - dołącz do nas i poczuj magię ruchu!",
        },
        // Dodaj więcej obiektów w zależności od potrzeb
    ];

    return (
        <div className="d-flex">
            {dataFromDatabase.map((data) => (
                <Card
                    key={data.id}
                    style={{width: '18rem', marginRight: '10px'}}
                >
                    <Card.Body>
                        <Card.Title>{data.title}</Card.Title>
                        <Card.Subtitle className="mb-2 text-muted">{data.subtitle}</Card.Subtitle>
                        <Card.Text>{data.text}</Card.Text>
                        <Button variant="dark">Zapisz się</Button>
                    </Card.Body>
                </Card>
            ))}
        </div>
    );
}

export default CourseCards;
