import React, {useEffect, useState} from 'react';
import QRCode from 'react-qr-code';
import './qr.css';
import axios from "axios";

import Button from 'react-bootstrap/Button';


function Qr() {
    const [value, setValue] = useState('200');
    const back = '#FFFFFF';
    const fore = '#000000';
    const qrSize = 200;

    const [showContent, setShowContent] = useState(false);
    const [userID] = useState(localStorage.getItem('id') || '200');
    const [userKey, setUserKey] = useState('200');
    const handleButtonClick = () => {
        setShowContent(!showContent);
    };

    const newQRButton = () => {

        setValue(23);
    }

    useEffect(() => {
        const getUserKey = async () => {
            try {
                const response = await axios.post('adres-endpointu-serwera', { text: userID });
                setUserKey(response.data);
            } catch (error) {
                console.error('Błąd:', error);
            }
        };

        getUserKey();
    }, []);


    const getNewUserKey = async () => {
        try {
            const response = await axios.post('adres-endpointu-serwera', { text: userID });
            setUserKey(response.data);
        } catch (error) {
            console.error('Błąd:', error);
        }
    };



    return (
        <div className="App">
            <center>
                <br/>
                <div className="button-continer">
                    <button
                        className={`button ${showContent ? 'active' : ''}`}
                        onClick={handleButtonClick}
                    >
                        {showContent ? 'Ukryj kod QR' : 'Pokaż kod QR'}
                    </button>
                    <button className="button" onClick={newQRButton}>
                        Wygeneruj nowy kod QR
                    </button>
                    <DelayedButton onClick={setValue} />
                </div>
                {showContent && (
                    <div>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        {value && (
                            <QRCode
                                title="QR"
                                value={value}
                                bgColor={back}
                                fgColor={fore}
                                size={qrSize}
                            />
                        )}
                    </div>
                )}
            </center>
        </div>
    );
}

export default Qr;


const DelayedButton = ({ onClick }) => {
    const [isButtonDisabled, setButtonDisabled] = useState(false);

    const handleButtonClick = () => {
        if (!isButtonDisabled) {
            setButtonDisabled(true);

            onClick("sadasdasdsadsadasd");
            setTimeout(() => {
                setButtonDisabled(false);
            }, 5000); // Czas opóźnienia w milisekundach (tutaj: 2 sekundy)
        }
    };

    return (
        <button
            className={`button ${isButtonDisabled ? 'disabled' : ''}`}
            onClick={handleButtonClick}
        >
            Przycisk
        </button>
    );
};